.image{
  /* background-color: rgb(72, 64, 64); */
  background-color: whitesmoke;
  animation: colorChange 5s infinite alternate;
  display: flex;
  padding-top: 4rem;
  padding-bottom: 1rem;
  gap: 2px;
}
.homeimage img{
  margin-top: 2rem;
  margin-left: 5rem;
  height: 25rem;
  width: 25rem;
  padding: 3rem;
  border: 2px solid #ddd;
  box-shadow: 0 4px 8px rgb(0,0,0,0.1);
  overflow: hidden;
  opacity: 1;
  background-color: rgb(0,0,0,0.5);
  clip-path: circle(50%);
}
