.homeimage:hover{
  transform: scale(1.05);
  opacity: 0.5;
  animation: none;
}
.homeimage{
  transition: 0.5s;
}
.home-content h1 {
  max-width: 33rem;
  background: linear-gradient(to right,#30cfd0 0%, #330867 100%);
  background-clip: text;
  color: transparent;
}

.home-content h3 {
  font-size: 3.2rem;
  /* color: whitesmoke; */
  color: black;
  
}

 h1 {
  font-size: 4rem; 
  margin-bottom: 2rem;
  /* color: whitesmoke; */
  color: black;
}

.home-content p {
  font-size: 1rem;
  /* color: whitesmoke; */
  color: black;
}

.about {
  /* background-color: rgb(72, 64, 64); */
  background-color: whitesmoke;
  display: flex;
  gap: 100px;
}

.about-img img {
  margin-top: 2rem;
  margin-right: 5rem;
  height: 25rem;
  width: 25rem;
  padding: 3rem;
  border: 2px solid #ddd;
  box-shadow: 0 4px 8px rgb(0,0,0,0.1);
  overflow: hidden;
  opacity: 1;
  background-color: rgb(0,0,0,0.5);
  clip-path: circle(50%);
}
.about-img:hover{
  transform: scale(1.05);
  opacity: 0.5;
}

.heading {
  margin-top: 3rem;
  font-size: 3rem;
  text-align: center;
  /* color: whitesmoke; */
  color: black;
}

.about-content h3,
.about-content h2 {
  text-align: left;
  font-size: 2rem;
  /* color: whitesmoke; */
  color: black;
}



.about-content p {
  font-size: 1rem;
  margin: 2rem 0 3rem;
  /* color: whitesmoke; */
  color: black;
}
.btns {
  text-align: center;
  margin-bottom: 3rem;
  background-color:  rgb(156, 238, 238);
  padding: 4px;
  text-decoration: none;
}
.btns:hover {
  background-color:  cyan;
}

.services {
  min-height: 3rem;
  padding-bottom: 10rem;
  /* background-color:  rgb(72, 64, 64); */
  background-color: whitesmoke;
}


.services h2 {
  margin-bottom: 5rem;
}

.services .services-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.services-container .services-box {
  flex: 1 1 1rem;
  background: rgb(125, 122, 122);
  padding: 3rem 2rem 4rem;
  border-radius: 1rem;
  box-shadow: 0 1rem 5rem;
  text-align: center;
  border-top: 1rem;
  border-bottom: 3rem;
  opacity: 0.5;
 
}

.services-container .services-box:hover{
    font-size: 20px;
    transform: scale(0.9);
    box-shadow: 5px 5px 5px grey;
    transition: transform 0.5s;
    opacity: 1;
    animation: moveRight 2s linear infinite;
}

@keyframes moveRight {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100px);
  }
}



.services-box h3 {
  transition: .5s ease;
  /* color: white; */
  color: black;
}


.services-box p {
  margin: 1rem 0 3rem;
  /* color: whitesmoke; */
  color: black;
}

.input-box{
  display: flex;
  gap: 9px;
}
.input-box input{
  width: 100%;
}

